import { Link } from 'react-router-dom';

import useLocales from '../../hooks/useLocales';

import Page from '../../components/Page';
import PageBanner from '../../components/wrappers/PageBanner';
import GradientBorderWrapper, { Divider } from '../../components/wrappers/GradientBorderWrapper';

export default function Identify() {
    const { t } = useLocales();

    return (
        <Page title={t('title.identity_verification')}>
            <PageBanner>
                <div className="container mb-[101px] p-4">
                    <div className="flex gap-2 justify-start text-xl mb-8">
                        <Link to="../">
                            <span className="text-primary underline">{t('title.menu.profile')}</span>
                        </Link>
                        <label>/</label>
                        <label>{t('title.identity_verification')}</label>
                    </div>
                    <div className="mb-16">
                        <label className="text-4xl md:text-[64px]">{t('title.identity_verification')}</label>
                    </div>

                    <div className="flex flex-col py-4 gap-3">
                        <GradientBorderWrapper>
                            <div className="p-6">
                                <div>
                                    <div className="flex gap-2 justify-between items-center">
                                        <h4 className="text-[28px]">{t('title.basic_fields')}</h4>
                                        <button className="btn btn-sm btn-neutral text-sm text-stone-400 capitalize px-1 py-0 md:px-4 md:py-2 h-auto">
                                            {t('action.no_required')}
                                        </button>
                                    </div>
                                    <p className="text-stone-400 mt-4">
                                        {t('description.basic_fields')}
                                    </p>
                                </div>
                                <Divider cls="mt-5" />
                                <div>
                                    <div className="flex gap-2 justify-between items-center">
                                        <h4 className="text-[28px]">{t('title.certificate')}</h4>
                                        <button className="btn btn-sm btn-neutral text-sm text-stone-400 capitalize px-1 py-0 md:px-4 md:py-2 h-auto">
                                            {t('action.no_required')}
                                        </button>
                                    </div>
                                    <p className="text-stone-400 mt-4">{t('description.certificate')}</p>
                                </div>
                                <Divider cls="mt-5" />
                                <div>
                                    <div className="flex gap-2 justify-between items-center">
                                        <h4 className="text-[28px]">{t('title.identity_verification')}</h4>
                                        <button className="btn btn-sm btn-neutral text-sm text-stone-400 capitalize px-1 py-0 md:px-4 md:py-2 h-auto">
                                            {t('action.no_required')}
                                        </button>
                                    </div>
                                    <p className="text-stone-400 mt-4">
                                        {t('description.identity_verification')}
                                    </p>
                                </div>
                            </div>
                        </GradientBorderWrapper>
                    </div>
                </div>
            </PageBanner>
        </Page>
    );
}
